import Vue from "vue";
import VueRouter from "vue-router";
import Role from "../views/Role.vue";
import Weapon from "../views/Weapon.vue";

Vue.use(VueRouter);

const newerouter = [
  { 
    path: '/Role', 
    component: Role,
    name: 'Role' 
  },{ 
    path: '/Weapon', 
    component: Weapon 
  }
];
const router = new VueRouter({
  // mode: "history",    //默认 hash 模式 —— 使用 URL 的 hash 来模拟一个完整的 URL，于是当 URL 改变时，页面不会重新加载。
  base: process.env.BASE_URL,
  routes: newerouter
});

export default router;



