<template>
    <div id="roleInfoView" v-bind="role_Info">    
        <div id="infoView">
            <div id="closebtn" @click="closebtn"><img src="@/assets/img/downbtn.png"></div>
            <!-- <div class="rolesimg"><img src="img/roles/fire/roles/SSR10.jpg"></div> -->
            <div class="rolesinfo">     
                <img :src="R_role_info.img">
                <p>名称:{{R_role_info.name}}</p>
                <p>类型:<img :src="R_role_info.type_img"></p>
                <p>属性:<img :src="R_role_info.prop_img"></p>
                <p>稀有度:<img :src="R_role_info.star_img"></p>
                <p>技能:</p>
                <br/>
                <p>角色详情施工中</p>
            </div>
        </div>
    </div>
</template>
<script>
import Vuex from "vuex";
import Allrole from "@/assets/data/role/role.json"

export default {
    name:'roleInfo',
    data () {
        return {
            R_role_info:[],
            R_role_id:[],
        }
    },
    components:{
        Allrole
    },
    computed:{
        role_Info(){
            this.R_role_id = [];
            this.R_role_info = [];
            this.R_role_id = this.$store.state.getRole_id;
            if(this.$store.state.getRole_id != ''){
                for(let i in Allrole.Allrole){
                    if(this.R_role_id === Allrole.Allrole[i].id){
                        this.$store.commit('store_getRole_info', Allrole.Allrole[i]);
                        this.R_role_info = this.$store.state.getRole_info;
                    }
                }
                
            }

        }   
        
    },
    methods:{
        closebtn(){      
            document.getElementById("roleInfoView").style.height="0";
            
        }
        

    },

}
</script>
<style lang="sass" scoped>
    @import '~@/assets/css/RoleSearch.css'
</style>