<template>
    <div id="rolesShowView" v-bind="roleList">
        <div id="roleItem">
      <div id="roleSearchInfo">
          <p>符合条件的角色有 {{roleNum}} 个</p> 
      </div>
      <div id="roleGird">
        <div class="roleItem"  @click="roleshowView(img.id)"  v-for="(img,i) in getRoleImg" :key="i" :id="img.id">
            <img class="roleV" :src="img.img">
            <img class="roleP" :src="img.prop_img">
        </div>
    </div>
  </div>
    </div>
</template>
<script>
import Vuex from "vuex";
import Allrole from "@/assets/data/role/role.json"
export default {
    name:'roleShow',
    data () {
        return {
            roleNum:"", 
            getAllRole:[],
            getRoleImg:[],
            getRoleId:[]
        }
    },
    components:{
        Allrole
    },
    computed:{
        roleList(){       
            this.getAllRole = Object.values(this.$store.state.getAll_obj);
            function getRoleList(item){return item != ''};
            const cpuRoleList = this.getAllRole.filter(getRoleList);
            //check 0 or check all 
            if(cpuRoleList.length === 0 || cpuRoleList.length === 16){
                this.getRoleImg =[];
                const getRoleImg_all=[];
                for(const a in Allrole.Allrole){
                    getRoleImg_all.push({id:Allrole.Allrole[a].id,img:Allrole.Allrole[a].img,prop_img:Allrole.Allrole[a].prop_img})
                };
               this.getRoleImg = getRoleImg_all;
               this.roleNum = this.getRoleImg.length;
            }//check 1
            else if(cpuRoleList.length === 1){
                this.getRoleImg =[];
                const getRoleImg_all=[];
                for(const index in cpuRoleList){
                    for(const Aindex in Allrole.Allrole){
                        if(cpuRoleList[index] == Allrole.Allrole[Aindex].star || cpuRoleList[index] == Allrole.Allrole[Aindex].prop || cpuRoleList[index] == Allrole.Allrole[Aindex].type){
                             getRoleImg_all.push({id:Allrole.Allrole[Aindex].id,img:Allrole.Allrole[Aindex].img,prop_img:Allrole.Allrole[Aindex].prop_img})
                        }
                    }
                } 
                this.getRoleImg = getRoleImg_all;
                this.roleNum = this.getRoleImg.length;
            } //check N(N>=2||N<16) ---important part
            else{
                this.getRoleImg =[];
                const getRoleImg_all=[];
                
                this.roleNum = this.getRoleImg.length;
 
            }  
        }
        

    },
    methods:{
        roleshowView(getId){
            this.getRoleId = [];
            document.getElementById("roleInfoView").style.height="100%";           
            this.getRoleId = getId;
           
            for(let i in Allrole.Allrole){
                if(this.getRoleId === Allrole.Allrole[i].id){
                    this.$store.commit('store_getRole_id', this.getRoleId);

                }
            }
        },
    }

}
</script>

<style lang="sass" scoped>
    @import '~@/assets/css/RoleSearch.css'  
</style>