<template>
    <div id="rolesearch">
        <div id="RoleSearchBox">
            <input type="text" placeholder="输入关键字" id="SearchRole"/>
            <input type="button" value="搜索" id="RoleSearchButton" @click="RoleSearchButton">
        </div>
        <div id="RoleCheckbox">
            <ul id="starbox">
                <!-- <li id="allstar" :class="{checkActive:allstarActive}" @click="allStarCheck">全部</li> -->
                <li id="star5" :class="{checkActive:star5Active}" @click="star5Check"><img src="@/assets/img/star/star5.png"></li>
                <li id="star4" :class="{checkActive:star4Active}" @click="star4Check"><img src="@/assets/img/star/star4.png"></li>
                <li id="star3" :class="{checkActive:star3Active}" @click="star3Check"><img src="@/assets/img/star/star3.png"></li>
                <li id="star2" :class="{checkActive:star2Active}" @click="star2Check"><img src="@/assets/img/star/star2.png"></li>
                <li id="star1" :class="{checkActive:star1Active}" @click="star1Check"><img src="@/assets/img/star/star1.png"></li>
            </ul>
            <ul id="propbox">
                <!-- <li id="allprop" :class="{checkActive:allpropActive}" @click="allPropCheck">全部</li> -->
                <li id="fire" :class="{checkActive:fireActive}" @click="fireCheck"><img src="@/assets/img/prop/fire.png" title="火"></li>
                <li id="water" :class="{checkActive:waterActive}" @click="waterCheck"><img src="@/assets/img/prop/water.png" title="水"></li>
                <li id="thunder" :class="{checkActive:thunderActive}" @click="thunderCheck"><img src="@/assets/img/prop/thunder.png" title="雷"></li>
                <li id="nature" :class="{checkActive:natureActive}" @click="natureCheck"><img src="@/assets/img/prop/nature.png" title="风"></li>
                <li id="light" :class="{checkActive:lightActive}" @click="lightCheck"><img src="@/assets/img/prop/light.png" title="光"></li>
                <li id="dark" :class="{checkActive:darkActive}" @click="darkCheck"><img src="@/assets/img/prop/dark.png" title="暗"></li>
            </ul>
            <ul id="typebox">
                <!-- <li id="alltype" :class="{checkActive:alltypeActive}" @click="allTypeCheck">全部</li> -->
                <li id="fighter" :class="{checkActive:fighterActive}" @click="fighterCheck"><img src="@/assets/img/type/fighter.png" title="格斗"></li>
                <li id="knight" :class="{checkActive:knightActive}" @click="knightCheck"><img src="@/assets/img/type/knight.png" title="剑士"></li>
                <li id="ranged" :class="{checkActive:rangedActive}" @click="rangedCheck"><img src="@/assets/img/type/ranged.png" title="射击"></li>
                <li id="supporter" :class="{checkActive:supporterActive}" @click="supporterCheck"><img src="@/assets/img/type/supporter.png" title="辅助"></li>
                <li id="special" :class="{checkActive:specialActive}" @click="specialCheck"><img src="@/assets/img/type/special.png" title="特殊"></li>
            </ul> 
        </div>
    </div>
</template>
<script>
import Vuex from "vuex";
export default {
    name:"rolesearch",
    data () {
        return {
            //star chose
            star5Active:false,
            star4Active:false,
            star3Active:false,
            star2Active:false,
            star1Active:false,
            //prop chose
            fireActive:false,
            waterActive:false,
            thunderActive:false,
            natureActive:false,
            lightActive:false,
            darkActive:false,
            //type chose
            fighterActive:false,
            knightActive:false,
            rangedActive:false,
            supporterActive:false,
            specialActive:false,
        }
    },
    methods:{
        fireCheck(){
            this.fireActive =! this.fireActive;
            if(this.fireActive){
                this.$store.commit('store_propFire', 'fire');
                
            }else{
                this.$store.commit('store_propFire', '');
            }
        },
        waterCheck(){
            this.waterActive =! this.waterActive;
            if(this.waterActive){
                this.$store.commit('store_propWater', 'water');
            }else{
                this.$store.commit('store_propWater', '');
            }
        },
        thunderCheck(){
            this.thunderActive =! this.thunderActive;
            if(this.thunderActive){
                this.$store.commit('store_propThunder', 'thunder');
            }else{
                this.$store.commit('store_propThunder', '');
            }
        },
        natureCheck(){
            this.natureActive =! this.natureActive;
            if(this.natureActive){
                this.$store.commit('store_propNature', 'nature');
            }else{
                this.$store.commit('store_propNature', '');
            }
        },
        lightCheck(){
            this.lightActive =! this.lightActive;
            if(this.lightActive){
                this.$store.commit('store_propLight', 'light');
            }else{
                this.$store.commit('store_propLight', '');
            }
        },
        darkCheck(){
            this.darkActive =! this.darkActive;
            if(this.darkActive){
                this.$store.commit('store_propDark', 'dark');
            }else{
                this.$store.commit('store_propDark', '');
            }
        },
        star5Check(){
            this.star5Active =! this.star5Active;
            if(this.star5Active){
                this.$store.commit('store_roleStar5', 'star5');
            }else{
                this.$store.commit('store_roleStar5', '');
            }
        },
        star4Check(){
            this.star4Active =! this.star4Active;
            if(this.star4Active){
                this.$store.commit('store_roleStar4', 'star4');
            }else{
                this.$store.commit('store_roleStar4', '');
            }
        },
        star3Check(){
            this.star3Active =! this.star3Active;
            if(this.star3Active){
                this.$store.commit('store_roleStar3', 'star3');
            }else{
                this.$store.commit('store_roleStar3', '');
            }
        },
        star2Check(){
            this.star2Active =! this.star2Active;
            if(this.star2Active){
                this.$store.commit('store_roleStar2', 'star2');
            }else{
                this.$store.commit('store_roleStar2', '');
            }
        },
        star1Check(){
            this.star1Active =! this.star1Active;
            if(this.star1Active){
                this.$store.commit('store_roleStar1', 'star1');
            }else{
                this.$store.commit('store_roleStar1', '');
            }
        },
        fighterCheck(){
            this.fighterActive=! this.fighterActive;
            if(this.fighterActive){
                this.$store.commit('store_typeFighter', 'fighter');
            }else{
                this.$store.commit('store_typeFighter', '');
            }
        },
        knightCheck(){
            this.knightActive=! this.knightActive;
            if(this.knightActive){
                this.$store.commit('store_typeKnight', 'knight');
            }else{
                this.$store.commit('store_typeKnight', '');
            }
        },
        rangedCheck(){
            this.rangedActive=! this.rangedActive;
            if(this.rangedActive){
                this.$store.commit('store_typeranged', 'ranged');
            }else{
                this.$store.commit('store_typeranged', '');
            }
        },
        supporterCheck(){
            this.supporterActive=! this.supporterActive;
            if(this.supporterActive){
                this.$store.commit('store_typeSupporter', 'supporter');
            }else{
                this.$store.commit('store_typeSupporter', '');
            }
        },
        specialCheck(){
            this.specialActive=! this.specialActive;
            if(this.specialActive){
                this.$store.commit('store_typeSpecial', 'special');
            }else{
                this.$store.commit('store_typeSpecial', '');
            }
        },
        RoleSearchButton(){
            alert("搜索功能施工中~")
        },
    }
}
</script>
<style lang="sass" scoped>
     @import '~@/assets/css/RoleSearch.css'
     
</style>